.item{
    width:350px;
}
.item p{
    margin:6px 0px;
}
.item-prices{
    display: flex;
    gap: 20px;
}
.item-prices-new{
       color:gray;
       font-size: 18px;
       font-weight: 600;
}
.item-prices-old{
    color:gray;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}
.item:hover{
    transform: scale(1.05);
    transition: 0.6s;
}